<template>
  <div>
    <p class="tasks">Completed Tasks: {{ todos.filter(todo => {return todo.done === true }).length }}</p>
    <p class="tasks">Pending Tasks: {{ todos.filter(todo => {return todo.done === false }).length }}</p>
      <!-- Now passing the data to the todo component to render the todo list -->
      <!-- Using v-bind to pass down data for each todo components -->
    <todo v-for="(todo,index) in todos" :key="index" v-on:delete-todo="deleteTodo" v-on:complete-todo="completeTodo" :todo.sync="todo"></todo>
  </div>
</template>

<script>

import Todo from './Todo';

export default {
  props: ['todos'],
  components: {
    Todo,
  },
  methods: {
    deleteTodo(todo) {
      // Select the position of the target array's index
      const todoIndex = this.todos.indexOf(todo);
      // use splice command to create a new object constists of the deleting todo
      this.todos.splice(todoIndex, 1);
    },
    completeTodo(todo) {
      const todoIndex = this.todos.indexOf(todo);
      this.todos[todoIndex].done = true;
    },
  },

};
</script>


<style scoped>
p.tasks {
  text-align: center;
}
</style>



