<template>
  <div id="app">

    <todo-list v-bind:todos="todos"></todo-list>
    <!-- Whenever there is an emition from other components called 'add-todo' execute the function called addTodo define on the methods scoped -->
    <add-new-todo v-on:add-todo="addTodo"></add-new-todo>
  </div>
</template>

<script>
// import HelloWorld from './components/HelloWorld';
import TodoList from './components/TodoList';
import AddNewTodo from './components/AddNewTodo';

export default {
  name: 'app',
  components: {
    TodoList,
    AddNewTodo,
  },
  // Data function avails data to the template
  data() {
    return {
      todos: [{
        title: 'Todo A',
        project: 'Project A',
        done: false,
      }, {
        title: 'Todo B',
        project: 'Project B',
        done: true,
      }, {
        title: 'Todo C',
        project: 'Project C',
        done: false,
      }, {
        title: 'Todo D',
        project: 'Project D',
        done: false,
      }],
    };
  },
  methods: {
    addTodo(newObj) {
      this.todos.push(newObj);
    },
  },
};
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>



